
html,body {
  margin: 0;
  padding: 0;
  height: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}
#music {
  position: absolute;
  width: 8vw;
  height: 8vw;
  z-index: 2;
  border-radius: 50%;
  right: 3vw;
  top: 3vw;
}
.musicPlaying {
  -webkit-animation: 1s rotate infinite linear;
          animation: 1s rotate infinite linear;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}
@-webkit-keyframes rotate {
from {-webkit-transform: rotate(0deg);transform: rotate(0deg)
}
25% {-webkit-transform: rotate(90deg);transform: rotate(90deg)
}
50% {-webkit-transform: rotate(180deg);transform: rotate(180deg)
}
75% {-webkit-transform: rotate(270deg);transform: rotate(270deg)
}
to {-webkit-transform: rotate(360deg);transform: rotate(360deg)
}
}
@keyframes rotate {
from {-webkit-transform: rotate(0deg);transform: rotate(0deg)
}
25% {-webkit-transform: rotate(90deg);transform: rotate(90deg)
}
50% {-webkit-transform: rotate(180deg);transform: rotate(180deg)
}
75% {-webkit-transform: rotate(270deg);transform: rotate(270deg)
}
to {-webkit-transform: rotate(360deg);transform: rotate(360deg)
}
}
#music div {
  width: 100%;
  height: 100%;
  font-size: 8.1vw;
}

#heart_click {
    width:  100%;
    height:  100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    position: relative;
    background-color:azure;
}
#click_tip {
    position: absolute;
    /* top: 50%; */
    /* left: 50%; */
    /* transform: translate(-50%,-50%); */
}
#heart {
    width: 15vw;
    height: 15vw;
    background-color: crimson;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    position: relative;
}
#heart::after,#heart::before {
    position: absolute;
    content: '';
    display: block;
    width: 15vw;
    height: 15vw;
    background-color: crimson;
    border-radius: 50%;
}
#heart::before {
    left: -7.5vw;
    top: 0;
}
#heart::after {
    right: 0;
    top: -7.5vw;
}

#Intro {
    width: 100%;
    height: 100%;
    background-color:lightpink;
}
#IntroContent {
    width: 100%;
    height: 100%;
    text-align: center;
    /* padding-top: 30%; */
}
#IntroTitle {
    font-size: 32px;
    color:firebrick;
    padding-top: 30%;
    padding-bottom: 20%;
}
#IntroSentence li {
    list-style: none;
    line-height: 17vw;
    color:darkred;
    font-size: 18px;
}
#Name {
    padding-top: 10%;
    padding-right: 5vw;
    text-align: right;
    font-size: 22px;
    color:dimgray;
}
#forever {
    position: absolute;
    width: 100%;
    text-align: center;
    bottom: 5vw;
    color:indianred;
    font-family: 'Tinos', serif;
}

#time,
#time-content {
  width: 100%;
  height: 100%;
}
#time-content {
  position: relative;
}
#img {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}
#together,
#already {
  text-align: center;
  font-size: 30px;
  color: dimgray;
}
#together {
  padding-top: 20%;
  padding-bottom: 5%;
}
#totalTime {
  position: absolute;
  width: 100%;
  text-align: center;
  bottom: 20%;
  font-family: "sans-serif";
  color: #666;
  font-size: 18px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.number {
  font-size: 32px;
  font-family: 'Tinos', serif;
}
#future {
  color: #666;
  text-align: center;
  position: absolute;
  bottom: 10%;
  width: 100%;
}
#timeLayout {
  width:  80%;
}
[v-cloak] {
  display: none;
}
